.auth-form__signin-link {
  font-size: 14px;
  line-height: 1.2;
  text-align: center;
}

.auth-form__signin-link_active {
  text-decoration: none;
  margin-left: 5px;
  color: white;
  transition: opacity .3s ease;
}

.auth-form__signin-link_active:hover {
  opacity: .4;
  cursor: pointer;
}